/* Table Styles */

.searchBar {
    font-family: 'Silka', sans-serif;
}

.table-wrapper {
    /* margin: 0 auto; */
    position: relative;
    /* top: 100px; */
    /* left: vw; */
    border-radius: 10px;
}

.fl-table {
    border-radius: 20px;
    font-size: 15px;
    font-weight: normal;
    border: none;
    border-collapse: collapse;
    width: 75vw;
    max-width: 100%;
    white-space: nowrap;
    background-color: rgb(240, 240, 240);
}

.fl-table td,
.fl-table th {
    text-align: center;
    padding: 8px;
}

.fl-table td {
    border-right: 1px solid #f8f8f8;
    font-size: 15px;
    font-family: 'Silka', sans-serif;
    padding: 10px;
}

.fl-table td:hover {
    cursor: pointer;
    background: rgb(99, 99, 99);
    color: white;
    font-family: 'Silka', sans-serif;
    font-weight: 100;
}

.fl-table thead th {
    color: #fcfcfc;
    background: #007bff;
    font-family: 'Silka', sans-serif;
    font-weight: 800;
    padding: 13px;
    text-shadow: 1px 1px 1px rgba(61, 61, 61, 0.076);
    text-decoration: none;
    -webkit-user-select: none;
    /* Chrome all / Safari all */
    /* Firefox all */
    -ms-user-select: none;
    /* IE 10+ */
    user-select: none;
    /* Likely future */
}

.fl-table thead th:hover {
    background: #3798ff;
    border: 1px solid white;
}

.fl-table thead th:nth-child(odd) {
    color: #007bff;
    background: rgb(248, 248, 248);
    font-family: 'Silka', sans-serif;
    font-weight: 800;
    text-decoration: none;
    -webkit-user-select: none;
    /* Chrome all / Safari all */
    /* Firefox all */
    -ms-user-select: none;
    /* IE 10+ */
    user-select: none;
    /* Likely future */
}

.fl-table thead th:nth-child(odd):hover {
    background: rgb(228, 228, 228);
    border: 1px solid white;
}

.fl-table tr:hover {
    background: rgb(189, 189, 189);
    transition: 0.2s ease-in;
}

.fl-table tr:nth-child(even) {
    background: #F8F8F8;
}

.fl-table tr:nth-child(even):hover {
    background: rgb(189, 189, 189);
    transition: 0.2s ease-in;
}

.fl-table tfoot {
    background: rgb(232, 232, 232)
}

.paginationBox {
    margin: 0 auto;
    position: relative;
    padding: 15px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-family: 'Silka', sans-serif;
}

.paginationButtons {
    margin-left: 10px;
}

.prevButton {
    border: none;
    position: relative;
    border-radius: 5px;
    padding: 10px;
    font-family: 'Silka', sans-serif;
    background: #007bff;
    color: #fff;
    cursor: pointer;
    margin-right: 1rem;
}

.nextButton {
    border: none;
    background: #007bff;
    border-radius: 5px;
    padding: 10px;
    font-family: 'Silka', sans-serif;
    color: #fff;
    cursor: pointer;
}

.prevButton:hover,
.nextButton:hover {
    color: black;
    background: white;
    box-shadow: 1px 1px 1px rgba(0, 0, 0, 0.549);
    transition: 0.1s ease-in;
}

.firstPage,
.lastPage {
    border: none;
    background: #007bff;
    border-radius: 5px;
    padding: 10px;
    font-family: 'Silka', sans-serif;
    color: #fff;
    cursor: pointer;
    margin: 0 20px;
}

.firstPage:hover,
.lastPage:hover {
    color: black;
    background: white;
    box-shadow: 1px 1px 1px rgba(0, 0, 0, 0.549);
    transition: 0.1s ease-in;
}

select {
    padding: 10px;
    border: 1px solid rgb(61, 151, 211);
    border-radius: 5px;
    background: white;
    font-family: 'Silka', sanf-serif;
    margin: 0 10px;
}


/* Responsive */

@media (max-width: 767px) {
    .fl-table {
        display: block;
        width: 100%;
    }
    .table-wrapper:before {
        content: "Scroll horizontally >";
        display: block;
        text-align: right;
        font-size: 11px;
        color: white;
        padding: 0 0 10px;
    }
    .fl-table thead,
    .fl-table tbody,
    .fl-table thead th {
        display: block;
    }
    .fl-table thead th:last-child {
        border-bottom: none;
    }
    .fl-table thead {
        float: left;
    }
    .fl-table tbody {
        width: auto;
        position: relative;
        overflow-x: auto;
    }
    .fl-table td,
    .fl-table th {
        padding: 20px .625em .625em .625em;
        height: 60px;
        /* display: initial; */
        vertical-align: middle;
        box-sizing: border-box;
        overflow-x: hidden;
        overflow-y: auto;
        width: 120px;
        font-size: 13px;
        text-overflow: ellipsis;
    }
    .fl-table thead th {
        text-align: left;
        border-bottom: 1px solid #f7f7f9;
    }
    .fl-table tbody tr {
        display: table-cell;
    }
    .fl-table tbody tr:nth-child(odd) {
        background: none;
    }
    .fl-table tr:nth-child(even) {
        background: transparent;
    }
    .fl-table tr td:nth-child(odd) {
        background: #F8F8F8;
        border-right: 1px solid #E6E4E4;
    }
    .fl-table tr td:nth-child(even) {
        border-right: 1px solid #E6E4E4;
    }
    .fl-table tbody td {
        display: block;
        text-align: center;
    }
}
